import React, { useState, useEffect } from "react"
import { PageProps, Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import { makeStyles } from '@material-ui/core/styles';
import Container from "@material-ui/core/Container";
import Toolbar from "@material-ui/core/Toolbar";

import storeData from "../resource/StoreData";

import beanBackground from "../resource/img/bean-bg.png";
import zBeansLogo from "../resource/img/logo720.png";

const useStyles = makeStyles((theme) => ({
	container: {
		backgroundImage: `url(${beanBackground})`,
		backgroundRepeat: "no-repeat",
		// backgroundPosition: "0px 250px",
		backgroundSize: "cover",
	},
	storeButton: {
		fontFamily: "'Josefin Slab', serif",
		fontWeight: 600,
		fontSize: "1.5em",
		lineHeight: "1em",
		color: "#ffffff",
		backgroundColor: "transparent",
		border: "1px solid #ffffff",
		borderRadius: "0.5em",
		padding: "0.75em",
		margin: "0.75em 0",
		cursor: "pointer",
		overflow: "hidden",
		"&:hover": {
			backgroundColor: "#ffffff",
			color: "saddlebrown",
		}
	},
	selectedStoreButton: {
		backgroundColor: "#ffffff",
		color: "saddlebrown",
	},
	location: {
		fontSize: "0.8em",
		fontWeight: 500,
		whiteSpace: "nowrap",
	},
	socialButton: {
		padding: "0.7em",
		fontSize: "1.7em",
		textAlign: "center",
		textDecoration: "none",
		margin: "0.5em",
		background: "white",
		color: "#42201e",
		borderRadius: "12px",
		"&:hover": { 
			opacity: 0.7,
		}
	}
}));

type Data = {
	site: {
		siteMetadata: {
			title: string
		}
	}
}

const StoreSelectLayout = ({ location, title, children }) => {

	return (
		<Layout location={location} title={title}>
			<div
				style={{
					height: "100vh",
				}}
				className="storeSelectLayout"
			>
				<main
					style={{
						padding: `0`,
						height: "100%",
					}}
				>
					{children}
				</main>
			</div>
		</Layout>
	)
}

const StoreSelectPage = ({ data, location }: PageProps<Data>) => {
	const siteTitle = data.site.siteMetadata.title

	const classes = useStyles();

	const [selectedStore, setSelectedStore] = useState(null);

	useEffect(() => {
		let currentStore = window.localStorage.getItem("store");
		if (currentStore === null) {
			currentStore = JSON.stringify(storeData[0]);
			window.localStorage.setItem("store", currentStore);
		}
		currentStore = JSON.parse(currentStore);
		setSelectedStore(currentStore);
	}, []);

	useEffect(() => {
		window.localStorage.setItem("store", JSON.stringify(selectedStore));
	}, [selectedStore]);

	return (
		<StoreSelectLayout location={location} title={siteTitle}>
			<SEO title="Store Select" />
			<div className={classes.container} style={{display: "flex", flexDirection: "column", height: '100%', overflowY: "auto"}}>
				<Container maxWidth="sm" style={{display: "flex", flexDirection: "column"}}>
					<div style={{backgroundColor: "#ffffff", borderRadius: "50%", width: "8em", height: "8em", margin: "1em auto"}}>
						<img src={zBeansLogo} alt="" style={{maxWidth: "100%"}} />
					</div>
					<h1 style={{color: "#ffffff", textAlign: "center", marginTop: 0}}>Select your cafe!</h1>
					<h3 style={{color: "#eeeeee", textAlign: "center", fontWeight: 600, marginTop: 0}}>Be sure to select your cafe before ordering!</h3>
					{storeData.map((store, index) => (
						<button
							key={index}
							className={`${classes.storeButton} ${(selectedStore && selectedStore.name === store.name) ? classes.selectedStoreButton : ""}`}
							onClick={(e) => setSelectedStore(storeData[index])}
						>
							<span>{store.name}</span><br/>
							<span className={classes.location}>{store.address}</span><br/>
							<span className={classes.location}>{store.city}, {store.state} {store.zip}</span>
						</button>
					))}
				</Container>
				<Container maxWidth="sm" style={{display: "flex", justifyContent: "space-around"}} >
					<a href="https://www.facebook.com/ZBeansCoffee/" className={classes.socialButton + " fa fa-facebook"}></a>
					<a href="https://twitter.com/ZBeansCoffee" className={classes.socialButton + " fa fa-twitter"}></a>
					<a href="https://www.instagram.com/zbeanscoffee/" className={classes.socialButton + " fa fa-instagram"}></a>
					<a href="https://www.youtube.com/channel/UCdEeXNPmtiPDI5vDxuhSXAw" className={classes.socialButton + " fa fa-youtube"}></a>
				</Container>
				<Toolbar></Toolbar>
			</div>
		</StoreSelectLayout>
	)
}

export default StoreSelectPage;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
